//
// Buttons
// --------------------------------------------------

@import './base';

// Base styles for Button component
// --------------------------------------------------
.btn {
  font-weight: @btn-font-weight;

  display: inline-block;

  margin-bottom: 0;

  cursor: pointer;
  user-select: none;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;

  border: 1px solid transparent;
  background-image: none;

  touch-action: manipulation;

  .m-button-size(@padding-base-vertical; @padding-base-horizontal; @font-size-base; @line-height-base; @btn-border-radius-base; 40px; 100px; @btn-font-weight-semiBold);
  .m-button-variant(@btn-secondary-color; @btn-secondary-bg; @btn-secondary-border);

  &,
  &:active,
  &.active {
    &:focus,
    &.focus {
      outline: 5px auto -webkit-focus-ring-color;
      outline-offset: -2px;
    }
  }

  &:hover,
  &:focus,
  &.focus {
    color: @btn-default-color;
    text-decoration: none;
  }

  &:active,
  &.active {
    outline: 0;
    background-image: none;
    box-shadow: inset 0 3px 2px rgba(0,0,0,.15)
  }

  &.disabled,
  &[disabled],
  fieldset[disabled] & {
    cursor: @cursor-disabled;
    opacity: .45;
    box-shadow: none
  }

  a& {
    line-height: 1.625;
    &.disabled,
    fieldset[disabled] & {
      pointer-events: none;
    }
  }
}


// Alternate styling buttons
// --------------------------------------------------
.btn-strong {
  .m-button-variant(@btn-strong-color; @btn-strong-bg; @btn-strong-border);
}

.btn-primary {
  .m-button-variant(@btn-primary-color; @btn-primary-bg; @btn-primary-border);
}

.btn-secondary {
  .m-button-variant(@btn-secondary-color; @btn-secondary-bg; @btn-secondary-border);
}


// Button Sizes
// --------------------------------------------------

.btn--xs,
.btn-xs {
  .m-button-size(@padding-xs-vertical; @padding-xs-horizontal; @font-size-xs; 16px; @btn-border-radius-xs; 20px; 50px; @btn-font-weight);
}

.btn--sm,
.btn-sm {
  .m-button-size(@padding-small-vertical; @padding-small-horizontal; @font-size-sm; 18px; @btn-border-radius-sm; 30px; 50px; @btn-font-weight);
}

.btn--lg,
.btn-lg {
  .m-button-size(@padding-large-vertical; @padding-large-horizontal; @font-size-lg; 24px; @btn-border-radius-lg; 50px; 150px; @btn-font-weight-semiBold);
}

.btn--xl,
.btn-xl {
  .m-button-size(@padding-xl-vertical; @padding-xl-horizontal; @font-size-xl; 36px; @btn-border-radius-xl; 60px; 170px; @btn-font-weight-semiBold);
}


// Button Socials
// --------------------------------------------------

.btn-social {
  &,
  &:hover {
    color: @white;
    min-width: 130px;
  }

  [class^='icon-'],
  [class*=' icon-'] {
      font-size: 44px;
      line-height: 26px;
      vertical-align: middle;

      margin: 0;
      padding: 0;
  }
}

.btn-social--facebook {
  .button-social(@facebook-button-color, @facebook-button-border-color);
}

.btn-social--twitter {
  .button-social(@twitter-button-color, @twitter-button-border-color);
}

.btn-social--google {
  .button-social(@google-button-color, @google-button-border-color);
}

.btn-social--linkedin {
  .button-social(@linkedin-button-color, @linkedin-button-border-color);
}

.btn-social--youtube {
  .button-social(@youtube-button-color, @youtube-button-border-color);
}

.btn-social--vimeo {
  .button-social(@vimeo-button-color, @vimeo-button-border-color);
}

.btn-social--instagram {
  .button-social(@instagram-button-color, @instagram-button-border-color);
}

.btn-social--flickr {
  .button-social(@flickr-button-color, @flickr-button-border-color);
}

.btn-social--foursquare {
  .button-social(@foursquare-button-color, @foursquare-button-border-color);
}

.btn-social--tumblr {
  .button-social(@tumblr-button-color, @tumblr-button-border-color);
}





